/**
 * Calculer un montant en fonction d'une base de calcul
 * et de ses "tranches" correspondantes.
 *
 * @TODO : flagguer les tranches "actives"  true et faire ainsi le total
 * plutt que de faire un array tranches Actives
 * Cela permettra de les voir dans le rsultat final, surlignes en jaune
 * et de voir quand on risque de passer  la tranche suivant, on pourra
 * mme mettre des icne pour indiquer qu'on risque de passer  la tranche suivante
 */
import _ from "lodash";

const service = {};

/**
 * Retourne le montant pour une tranche de result.
 *
 * @param tranche object
 *   - label
 *   - montant : peut tre dj rempli pour les montants forfaitaires
 *   - taux : le pourcentage  appliquer sur le montant
 *   - montant_forfaitaire : si la tranche est un montant fixe en fonction du plafond.
 * @param baseCalcul float
 */
service.calculerMontantTranche = (tranche, baseCalcul) => {
  let montant = 0;
  if (tranche.montant_forfaitaire) {
    montant = tranche.montant_forfaitaire;
  }
  else if (tranche.taux) {
    montant = baseCalcul * (tranche.taux / 100);
  }
  return _.round(montant, 2);
};

/**
 * Calcul la tranche qui correspond  baseCalcul en fonction du tableau "tranches".
 * Pour les tranches exclusives, seule UNE tranche est conserv pour le calcul, les
 * tranches prcdentes ou suivantes n'entrent donc en rien dans le calcul du montant
 * de la cotisation
 *
 * @param baseCalcul float
 * @param tranches array d'objet tranche
 *
 * @return object {montant, baseCalcul, trancheActive}
 */
service.calculerTrancheExclusive = (baseCalcul, tranches) => {

  let result = {};

  result.montant = 0;

  // on recherche la tranche qui correspond  notre baseCalcul
  let trancheActive = null;

  tranches.forEach(function(tranche) {
    // tant que la base de calcul n'est pas suprieur au plafond en cours, on continue
    // d'itrer.
    if (!trancheActive && baseCalcul <= tranche.plafond) {
      // on a dpass le plafond, on arrte de mettre  jour la variable trancheActive
      // qui contient maintenant notre rponse
      trancheActive = tranche;
    }
  });

  if (trancheActive) {
    result.montant = service.calculerMontantTranche(trancheActive, baseCalcul);
    result.baseCalcul = baseCalcul;
    // l'objet lineResult attend un tableau pour tranchesActive
    result.tranchesActives = [trancheActive];
  }

  return result;
};

/**
 * Calcul des results  tranches cumulatives, tels que l'impot sur les bnfices :
 * 15% pour pour les 38120 premiers euros, puis on ajoute 33,33% sur le reste des bnfices
 *
 * @param baseCalcul float
 * @param result array : tableau d'objet "results"
 */
service.calculerTranchesCumulatives = (baseCalcul, tranches) => {

  let result = {};
  result.montant = 0;
  let tranchesActives = [];

  // contiendra la liste des tranches qui seront applique
  //  notre base de calcul

  // montant total, toute tranches cumules
  let montant = 0;
  let plancher = 0;

  tranches.forEach((tranche, index) => {

    // on calcule le "plancher" de la tranche, qui est soit gal
    // au plafond prcdent, soit  zro si c'est la premire tranche.
    if (typeof tranches[index - 1] !== 'undefined') {
      plancher = tranches[index - 1].plafond;
    }
    // on calcule la diffrence entre le plafond et le plancher
    tranche.intervalle = tranche.plafond - plancher;

    // si la somme est suprieure ou gale au plafond de la tranche courante ...
    if (baseCalcul >= tranche.plafond)
    {
      // ... on calcule le montant d pour la tranche courante
      tranche.baseCalcul = tranche.intervalle;
      tranche.montant = service.calculerMontantTranche(tranche, tranche.baseCalcul);
      // on ajoute le montant de la cotisation de cette tranche au total.
      montant += tranche.montant;
      // ajout  la liste des tranches qui s'applique  notre cas.
      tranchesActives.push(tranche);
    }
    // mais si la somme est infrieure au plafond courant, c'est que nous sommes  la dernire tranche
    else
    {
      // on calcule le montant pour cette derniere tranche
      let depassement_plancher = baseCalcul - plancher;
      if (depassement_plancher > 0)
      {
        tranche.baseCalcul = depassement_plancher;
        montant += tranche.montant = service.calculerMontantTranche(tranche, tranche.baseCalcul);
        // ajout  la liste des tranches qui s'appliquent  notre cas.
        tranchesActives.push(tranche);
      }
    }

  });

  result.montant = _.round(montant, 2);
  result.tranchesActives= tranchesActives;

  return result;
};

export default service;







